import type { Metadata } from 'next';
import { Playfair_Display, Inter } from 'next/font/google';
import './globals.css';
import { Sidebar } from '../components/Sidebar';
import { InternalLinkRecommendations } from '../components/InternalLinkRecommendations';

const playfair = Playfair_Display({
    subsets: ['latin'],
    variable: '--font-serif',
    display: 'swap',
});

const inter = Inter({
    subsets: ['latin'],
    variable: '--font-sans',
    display: 'swap',
});

export const metadata: Metadata = {
    title: "Sweety's Bakery - Best Online Bakery Resources & Cake Delivery Tools",
    description: "The ultimate resource for bakers and dessert lovers. Find tools for pricing wedding cakes, ordering birthday cakes online, and creating gourmet dessert gift boxes. Your guide to the best cake delivery in the US.",
};

export default function RootLayout({
    children,
}: {
    children: React.ReactNode;
}) {
    return (
        <html lang="en" className={`${playfair.variable} ${inter.variable}`}>
            <body className="flex min-h-screen bg-brand-background font-sans">
                <script
                    type="application/ld+json"
                    dangerouslySetInnerHTML={{
                        __html: JSON.stringify({
                            "@context": "https://schema.org",
                            "@type": "WebSite",
                            "name": "Sweety's Bakery",
                            "url": "https://www.sweetysbakery.com/",
                            "potentialAction": {
                                "@type": "SearchAction",
                                "target": "https://www.sweetysbakery.com/search?q={search_term_string}",
                                "query-input": "required name=search_term_string"
                            }
                        })
                    }}
                />
                {/* Sidebar will need to be refactored to be a client component or handle nav differently */}
                <Sidebar />
                <div className="flex-1 flex flex-col">
                    {children}
                    <InternalLinkRecommendations />
                </div>
            </body>
        </html>
    );
}
